import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

// import http module
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';

// import subscribe module
import { Subscription } from 'rxjs/Subscription';

// import forms from module
import { FormsModule} from '@angular/forms';
// import ng2 toastr
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {ToastModule} from 'ng2-toastr/ng2-toastr';

import { ShowHidePasswordModule } from 'ngx-show-hide-password';
import { Angular2FontawesomeModule } from 'angular2-fontawesome/angular2-fontawesome';


import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { ChangePwdComponent } from './change-pwd/change-pwd.component';
import { ForgotPwdComponent } from './forgot-pwd/forgot-pwd.component';
import { UnlockAcctComponent } from './unlock-acct/unlock-acct.component';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    ChangePwdComponent,
    ForgotPwdComponent,
    UnlockAcctComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    HttpClientModule,
    FormsModule,
    BrowserAnimationsModule,
    Angular2FontawesomeModule,
    ToastModule.forRoot(),
    ShowHidePasswordModule.forRoot(),
    RouterModule.forRoot([
      { path: 'login' , component: LoginComponent , pathMatch: 'full'},
      { path: '', redirectTo: 'login', pathMatch: 'full' },
      {path: 'change', component: ChangePwdComponent},
      {path: 'forgot', component: ForgotPwdComponent},
      {path: 'unlock', component: UnlockAcctComponent},
      { path: '**', component: LoginComponent},
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



